Variables */
/* =====================================================================================
Variables */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Basic styles */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 16px; }

img {
  max-width: 100%;
  height: auto; }

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0; }

* {
  box-sizing: border-box; }

/* =====================================================================================
Container */
.container {
  max-width: 1180px;
  width: 100%;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px; }

/* =====================================================================================
Icon-menu */
.icon-menu {
  display: none; }

@media (max-width: 992px) {
  .icon-menu {
    position: relative;
    display: block;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5; }
    .icon-menu span {
      position: absolute;
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #ffffff;
      transition: all 0.3s ease 0s; }
      .icon-menu span:first-child {
        top: 0; }
      .icon-menu span:last-child {
        top: auto;
        bottom: 0; }
    .icon-menu--active span {
      transform: scale(0); }
      .icon-menu--active span:first-child {
        top: calc(50% - 1px);
        transform: rotate(-45deg); }
      .icon-menu--active span:last-child {
        bottom: calc(50% - 1px);
        transform: rotate(45deg); } }

/* =====================================================================================
Header */
.header {
  background-image: url("../img/header/bg-header.jpg");
  background-repeat: no-repeat;
  background-position: top center; }

/* =====================================================================================
Top-header */
.top-header__inner {
  display: flex;
  align-items: center;
  min-height: 89px; }

.top-header__column {
  flex-basis: 50%; }

.top-header__column--logo {
  flex-shrink: 0;
  flex-basis: 190px;
  align-self: flex-start;
  text-align: center;
  padding-top: 27px; }

.top-header__column-link--logo {
  position: relative;
  z-index: 5; }

.top-header__contacts {
  position: relative;
  z-index: 5; }

@media (max-width: 992px) {
  .top-header__column--logo {
    flex-shrink: 0;
    flex-basis: 100px;
    align-self: flex-start;
    text-align: center;
    padding-top: 27px; } }

@media (max-width: 768px) {
  .top-header__column--logo {
    padding-top: 25px; } }

/* =====================================================================================
Menu */
.menu__list {
  display: flex;
  flex-wrap: wrap; }

.menu__list-item {
  flex-basis: 20%;
  margin-top: 1px;
  margin-bottom: 1px; }

.menu__list-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding-top: 5px;
  padding-right: 22px;
  padding-bottom: 5px;
  padding-left: 22px;
  height: 27px;
  font-family: "Play", "Helvetica", sans-serif;
  font-weight: 700;
  color: #ffffff;
  text-decoration: underline;
  transition: all 0.2s linear;
  white-space: nowrap; }
  .menu__list-link:hover {
    background-color: #068292;
    border-radius: 20px; }

@media (max-width: 1070px) {
  .menu__list-link {
    padding-right: 10px;
    padding-left: 10px; } }

@media (max-width: 992px) {
  .menu__body {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    display: none;
    width: 100%;
    height: 100%;
    padding-top: 70px;
    padding-right: 20px;
    padding-bottom: 30px;
    padding-left: 20px;
    background-color: #ffffff;
    overflow: auto;
    visibility: hidden; }
    .menu__body:before {
      content: "";
      position: fixed;
      top: 0;
      left: 0;
      z-index: 3;
      width: 100%;
      height: 89px;
      background-color: #0097a9; }
  .menu__body--active {
    display: block;
    visibility: visible; }
  .menu__list {
    flex-direction: column;
    margin-top: 25px; }
  .menu__list-item {
    margin-bottom: 20px;
    text-align: center; }
  .menu__list-link {
    font-size: 22px;
    color: #999999; } }

/* =====================================================================================
Contacts-header */
.contacts-header {
  display: flex;
  justify-content: center; }
  .contacts-header__column {
    padding-right: 30px;
    padding-left: 30px;
    border-left: 1px solid #ffffff; }
    .contacts-header__column:first-child {
      border: 0; }
  .contacts-header__item--icon {
    padding-left: 37px;
    background-image: url("../img/header/phone.png");
    background-repeat: no-repeat;
    background-position: left; }
  .contacts-header__label {
    margin-bottom: 5px;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    color: #ffffff; }
  .contacts-header__phone {
    font-size: 18px;
    font-weight: 900;
    line-height: 18px;
    text-decoration: none;
    color: #ffffff;
    white-space: nowrap; }

@media (max-width: 1180px) {
  .contacts-header {
    justify-content: flex-end; }
    .contacts-header__column {
      padding-right: 15px;
      padding-left: 15px; }
    .contacts-header__item--icon {
      padding-left: 25px;
      background-size: 15px; }
    .contacts-header__phone {
      font-size: 16px; } }

@media (max-width: 992px) {
  .contacts-header__column:last-child {
    display: none; } }

@media (max-width: 640px) {
  .contacts-header__column {
    padding-right: 0;
    padding-left: 0; }
  .contacts-header__item--icon {
    padding-left: 0;
    background-image: none; }
  .contacts-header__label {
    display: none; }
  .contacts-header__phone {
    display: inline-block;
    vertical-align: middle;
    width: 19px;
    height: 25px;
    font-size: 0;
    background-image: url("../img/header/phone.png");
